import React, { useContext, useState } from 'react';

import {
    Nav,
    Navbar,
    NavbarText,
} from "reactstrap";

import { AuthContext } from "@meddbriefer/mdb-auth"

import UserDropDown from "./UserDropDown"


const MDBNavBar = ({title, leftNav = null, rightNav = null }) => {

    const { isAuthenticated } = useContext(AuthContext)

    return (
        <Navbar>

            <NavbarText>
                <b>MedDBriefer &nbsp; </b>
            </NavbarText>

                {!!leftNav &&
                    <Nav>
                    {leftNav}
                    </Nav>
                }

                {!!title &&
                    <NavbarText className="mr-auto"> | &nbsp; <b> {title}</b></NavbarText>
                }

                <Nav>
                    {!!rightNav && rightNav}
                </Nav>
                <Nav>
                    <UserDropDown show={isAuthenticated} />
                </Nav>

        </Navbar>
    )
}

export default MDBNavBar;
