
import { useScenario } from "../contexts/ScenarioContext"

import CheckBox from "./CheckBox"
import CallOutText from "./CallOutText"
import CallOutIcon from "./icons/CallOutIcon"

const CheckListItem = ({step}) => {
    const {
        isChecked,
        toggleChecked,
        toggleAssessmentFinding,
    } = useScenario()

    switch (step.type) {
        case 'assessment':
            return (
                <>
                <CheckBox
                    step={step}
                    clickHandler={toggleAssessmentFinding}
                >
                </CheckBox>
                <span>
                    <CallOutIcon step={step} />
                    {isChecked(step.id) && <CallOutText step={step} /> }
                </span>
                </>
            )
        case "prompt":
            return (<i>{step.label}</i>)
        default:
            // case 'required-action':
            return (
                <CheckBox
                    step={step}
                    clickHandler={toggleChecked}
                />
            )
        }

}


export default CheckListItem;