import { useEffect, useState } from "react"

import {
    NavLink,
    Switch,
    useRouteMatch
 } from "react-router-dom"

import {
    Col,
    Container,
    Row
} from "reactstrap"

import { PrivateRoute } from "@meddbriefer/mdb-auth"

import UnauthedLayout from "./UnauthedLayout"
import { useScenario } from "../../contexts/ScenarioContext"
import { usePrefs } from "../../contexts/PreferencesContext"

import Phase     from "../../routes/Phase"
// import HeadToToe from "../../routes/HeadToToe"


import InterventionsList from "../InterventionsList"
import PhaseNavPane from "../PhaseNavPane"

import IntvForm from "../forms/IntvForm"
import InterventionsPerformed from "../InterventionsPerformed"

const PhaseDetailsLayout = () => {
    const { scenario, currIntv, currPhase, currSubPhase } = useScenario()
    // const { intvListStyle } = usePrefs()
    const { path, url} = useRouteMatch()
    // const [phases, setPhases] = useState([])
    const [form, setForm] = useState(null)

    // useEffect(() => {
    //     // if (headToToeStyle === "stick-figure") {
    //         // setPhases(scenario.phaseData.filter(rec => rec.uiNav === true))
    //     // } else {
    //         setPhases(scenario.phaseData)//.filter(rec => rec.dataEntry === true))
    //     // }
    // }, [headToToeStyle])

    const getSection = () => {
        return currSubPhase || currPhase || ""
    }

    useEffect(() => {
        if (!!currIntv) {
            setForm(scenario.interventionForms[currIntv])
        } else {
            setForm(null)
        }
    }, [currIntv])

    return (
        <Container fluid={true} className="main">
            <Row className="main-row">
                <nav className="col col-md-2 phase-nav-pane">
                    <PhaseNavPane />
                </nav>
                <div className="col-md-5 phase-left-col">
                    <Switch>
                        {/* <PrivateRoute
                            exact path={`${path}/head-to-toe`}
                            authLayout={UnauthedLayout}
                            component={HeadToToe}
                        /> */}
                        <PrivateRoute
                            path={`${path}/:phaseID`}
                            authLayout={UnauthedLayout}
                            component={Phase}
                        />
                        <PrivateRoute
                            exact path={path}
                            authLayout={UnauthedLayout}
                        >
                            <h3>Please select a section on the left.</h3>
                        </PrivateRoute>
                    </Switch>
                </div>
                <div className="col-md-5 phase-right-col">
                    <InterventionsPerformed />
                    {/* {"dropdown" === intvListStyle && */}
                    <InterventionsList section={getSection()}/>
                    {/* } */}
                    {!!form &&
                        <IntvForm
                            id={form.id}
                            label={form.label}
                            checkedWhen={form.checkedWhen}
                            prompts={form.prompts}
                        />
                    }
                </div>
            </Row>
        </Container>
    )
}


export default PhaseDetailsLayout