import { useState, useEffect } from "react"

import { useParams } from "react-router-dom"

import { useScenario } from "../contexts/ScenarioContext"
// import { usePrefs } from "../contexts/PreferencesContext"

import PhaseLayout from "../components/layouts/PhaseLayout"
import PhaseCheckList    from "../components/PhaseCheckList"
import InterventionsList from "../components/InterventionsList"

const Phase = () => {
    const { phaseID } = useParams()
    const { scenario, setCurrIntv, setCurrIntvCat, setShowIntvSelection } = useScenario()
    // const { intvListStyle } = usePrefs()
    const [phase, setPhase] = useState(null)

    useEffect(() => {
        setPhase(scenario.phaseData.find(rec => rec.id === phaseID))
    }, [phaseID])

    const getTop = () => <PhaseCheckList section={phase} />

    const getBottom = () => {
        if ("dropdown" === intvListStyle) {
            return (<div></div>)
        }
        return   ( <InterventionsList section={phase} />)
    }

    useEffect(() => {
        /* clear the current intervention */
        setCurrIntv(null)
        setCurrIntvCat("")
        setShowIntvSelection(true)
        // if (null === phase) {
        //     setCurrIntvCat("")
        // } else if (!!phase.interventionsMenu && typeof phase.interventionsMenu === "string" && "" !== phase.interventionsMenu) {
        //     /* this phase has an associated interventionsMenu (category) set it */
        //     setCurrIntvCat(phase.interventionsMenu)
        // } else {
        //     setCurrIntvCat("")
        // }
    }, [phase])

    if (!!phase) {
        return (
            <PhaseLayout
                top={getTop(phase)}
            />
        )
    } else {
        return (<div>phase not set yet</div>)
    }
}

export default Phase